@use "../common/variables.scss";

.form-contacts__label {
  display: flex;
  align-items: center;
  white-space: nowrap;
}

.form-contacts__input {
  padding-left: 15px;
  padding-top: 14px;
  padding-bottom: 13px;
  margin-left: 13px;
  width: 100%;
  border: 1px solid variables.$border-grey-input;

  &::placeholder {
    font-family: "Oswald", sans-serif;
    font-size: 16px;
    line-height: 18px;
    color: variables.$special-dark-grey;
    text-transform: uppercase;
  }
}
